.main {
  text-align: center;
}
.form {
  border: 5px dashed #ececec;
  padding: 10px 0px;
  text-align: center;
}
.upload_btn {
  padding: 15px 20px;
  margin: 30px;
  color: white;
  background-color: green;
  outline: none;
  border: 0px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
  border-radius: 3px;
  font-size: 18px;
}
.desc_line {
  margin-bottom: 10px;
}
.select_file_outer {
  margin-top: 10px;
}
.req_file_removal,
.copyright {
  color: gray;
}
.footer {
  margin-top: 20vh;
}
/* Error Box */
.error_main {
  width: 90%;
  border: 2px solid #aa0000;
  background-color: #ffcccc;
  margin: auto;
  height: 150px;
  margin-bottom: 20px;
}
/* Error Box */
.success_main {
  width: 90%;
  background-color: #ccffcc;
  border: 2px solid #00aa00;
  margin: auto;
  height: 150px;
  margin-bottom: 20px;
}
.hidden {
  display: none;
}